import React, { FunctionComponent, useState } from 'react'
import { Flex, Button, Box, BoxProps, Text, Input, InputGroup, InputRightAddon, useDisclosure } from '@chakra-ui/core'
import theme from "../../gatsby-plugin-chakra-ui/theme"
import Joi from "joi"
import addToMailchimp from "gatsby-plugin-mailchimp"
import SubscriptionResponse from "./SubscriptionResponse"
import { MailchimpResponse } from '../../models/mailchimp'

type Props = {
  compact?: boolean
} & BoxProps

const compactProps = {
  border: "none",
  padding: "0px",
  mt: "10px"
} as BoxProps

const Subscribe: FunctionComponent<Props> = ({compact = false, ...boxProps}) => {
  const [email, setEmail] = useState("")
  const [emailValid, setEmailValid] = useState(true)
  const [mcResponse, setMcResponse] = useState<MailchimpResponse>(null)
  const [subscribed, setSubscribed] = useState(false)
  const { onClose } = useDisclosure()

  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const email = e.currentTarget.value

    const emailSchema = Joi.string().email({tlds: {allow: false}})
    const { error, value } = emailSchema.validate(email.trim())

    if (error && error.message) {
      setEmailValid(false)
    } else {
      setEmailValid(true)
    }

    setEmail(value)
  }

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()

    if (!emailValid || email.length === 0) return

    const result = await addToMailchimp(email) as MailchimpResponse
    setMcResponse(result)
    setEmail("")
    setSubscribed(true)
  }

  const handleCloseSubscriptionResponse = () => {
    setSubscribed(false)
    onClose()
  }

  const containerProps = compact && compactProps || {}

  return (
    <Box padding="10px" borderWidth="2px" borderRadius="6px" borderColor={theme.colors.white} {...containerProps} {...boxProps}>
      {!compact && (
        <Text mb="10px">
          Subscribe to not miss out on new posts =)
        </Text>
      )}
      {subscribed && (
        <SubscriptionResponse
          response={mcResponse}
          isOpen={subscribed}
          onClose={handleCloseSubscriptionResponse}
        />
      )}
      <form onSubmit={handleSubmit} style={{marginBottom: "10px"}}>
        <Flex wrap="wrap">
          <InputGroup size="sm" flexGrow={1}>
            <Input
              value={email}
              type="email"
              onChange={handleEmailChange}
              placeholder="example@example.com"
              borderColor={theme.colors.white}
              focusBorderColor={theme.colors.white}
              color={theme.colors.black}
              isInvalid={!emailValid}
              fontSize="16px"
            />
            <InputRightAddon bg={theme.colors.accent} border="none" children={
              <Button
                onClick={handleSubmit}
                color={theme.colors.black}
                bg={theme.colors.accent}
                size="xs"
                borderWidth="0px"
                marginTop="0"
                height="100%"
                width="100%"
                border="none"
                borderRadius="0px"
                borderTopRightRadius="0.125rem"
                borderBottomRightRadius="0.125rem"
                paddingLeft="5px"
                paddingRight="5px"
                _hover={{
                  bg: `${theme.colors.accent}`
                }}
              >
                <Text fontSize="sm" color={theme.colors.black}>Subscribe!</Text>
              </Button>
            }/>
          </InputGroup>
        </Flex>
      </form>
    </Box>
  )
}

export default Subscribe
