import React, { useState } from "react"
import { useStaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"
import { Box, Text } from "@chakra-ui/core"
import { FiTwitter} from "react-icons/fi"
import { AiOutlineLinkedin, AiOutlineGithub } from "react-icons/ai"
import { RiBuilding3Line } from "react-icons/ri"
import theme from "../gatsby-plugin-chakra-ui/theme"
import { rhythm } from "../utils/typography"
import Subscribe from "./Subscribe"

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      site {
        siteMetadata {
          author {
            name
            summary
          }
          social {
            twitter
            linkedIn
            gitHub
          }
          companySiteUrl
        }
      }
    }
  `)

  const { author, social, companySiteUrl } = data.site.siteMetadata
  return (
    <>
    <Box display="flex" ml="10px" mr="10px" color={theme.colors.white}>
      <Box flexDirection="column" alignItems='center' mr="5px">
      </Box>
      <Box display="flex" flexDirection="column" width="100%">
        <Text as="p">
          Written by <strong>{author.name}</strong>{author.summary}
        </Text>
        <Box display="flex" flexDirection="row" justifyContent="flex-start" flexWrap="wrap">
          <a href={`https://twitter.com/${social.twitter}`} target="_blank" style={{textDecoration: 'none', boxShadow: 'none'}}>
            <Box display="flex" flexDirection="row" mr="10px" justifyContent="flex-start" alignItems="center" textDecoration="none">
              <Box as={FiTwitter} mr="5px"/>
              <Text>Twitter</Text>
            </Box>
          </a>
          <a href={`https://linkedIn.com/in/${social.linkedIn}`} target="_blank" style={{textDecoration: 'none', boxShadow: 'none'}}>
            <Box display="flex" flexDirection="row" mr="10px" justifyContent="flex-start" alignItems="center">
              <Box as={AiOutlineLinkedin} mr="5px"/>
              <Text>LinkedIn</Text>
            </Box>
          </a>
          <a href={`https://github.com/${social.gitHub}`} target="_blank" style={{textDecoration: 'none', boxShadow: 'none'}}>
            <Box display="flex" flexDirection="row" mr="10px" justifyContent="flex-start" alignItems="center">
              <Box as={AiOutlineGithub} mr="5px"/>
              <Text>GitHub</Text>
            </Box>              
          </a>
          <a href={companySiteUrl} target="_blank" style={{textDecoration: 'none', boxShadow: 'none',}}>
            <Box display="flex" flexDirection="row" justifyContent="flex-start" alignItems="center">
              <Box as={RiBuilding3Line} mr="5px"/>
              <Text>Company</Text>
            </Box>              
          </a>
        </Box>
        <Subscribe compact={true} />          
      </Box>
    </Box>
    </>
  )
}

export default Bio
