import React, { FunctionComponent, useMemo } from "react"
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  Button,
  Text,
} from "@chakra-ui/core"
import theme from "../../gatsby-plugin-chakra-ui/theme"
import { MailchimpResponse } from "../../models/mailchimp"

/*
  msg: "Thank you for subscribing!"
  result: "success"
*/

interface Props {
  response: MailchimpResponse
  isOpen: boolean,
  onClose: () => void
}

const SubscriptionResponse: FunctionComponent<Props> = ({ response, isOpen, onClose }) => {

  const success = response && response.result && response.result === "success"
  const message = useMemo<string>(() => {
    if (!response) return null

    if (response.result === "success") {
      return response.msg
    } else {
      if (response.msg.includes("already")) {
        return "You're already subscribed to my blog, but thanks for making sure =)"
      } else {
        return response.msg
      }
    }

  }, [response])


  return (
    <>
      <Modal isOpen={isOpen} onClose={onClose}>
      <ModalOverlay />
      <ModalContent backgroundColor={theme.colors.black} color={theme.colors.white}>
      <ModalHeader>{success ? "Successfully subscribed" : "Failed to subscribe"}</ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          <Text>
            {message}
          </Text>
        </ModalBody>
        <ModalFooter>
          <Button
            backgroundColor={theme.colors.accent} 
            color={theme.colors.black}
            onClick={onClose} 
            size="sm"
          >
            Close
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  </>
  )
}

export default SubscriptionResponse
